<template>
  <div v-if="user.id" class="container">
    <div>
      Avatar: <img width="32" height="32" :src="user.image || 'img/icons/favicon-32x32.png'">
      <div class="message">Hello <strong>{{ user.username }}</strong> !</div>
      <button @click.prevent="logout" class="button is-link">Logout</button>
      <div class="nav">
        <router-link to="/">Home</router-link> |
        <router-link to="/profile">Profile</router-link>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
import Auth from '@/api/auth'
import { mapGetters } from 'vuex'

export default {
  name: 'DefaultLayout',
  computed: {
    ...mapGetters({
      user: 'user/userDetails'
    })
  },
  methods: {
    logout () {
      Auth.logout()
      this.$router.push({ name: 'Login' })
    }
  }
}
</script>

